* {
  padding: 0;
  margin: 0;
}

body, html {
  font-family: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
  display: block;
}

#touch, #win {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

#win {
  color: #fff;
  display: none;
  opacity: 0;
  -webkit-transition: opacity 0.8s ease-in-out;
  -o-transition: opacity 0.8s ease-in-out;
  transition: opacity 0.8s ease-in-out;
}

#win.active {
  opacity: 1;
}

#win .button {
  opacity: 0;
  -webkit-transition: opacity 0.8s ease-in-out 0.8s;
  -o-transition: opacity 0.8s ease-in-out 0.8s;
  transition: opacity 0.8s ease-in-out 0.8s;
}

#win.active .button {
  opacity: 0.9;
}

#score {
  position: absolute;
  top: 8px;
  right: 8px;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 1px;
}

.screen-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

h1 {
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 15px;
  text-align: center;
}

.button {
  display: inline-block;
  position: relative;
  border: 0;
  font-size: 18px;
  cursor: pointer;
  padding: 10px 15px;
  background-color: #fff;
  color: #345069;
  border-radius: 3px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


